import React from "react";
import { connect } from "react-redux";
import {
  fetchPlaylists,
  fetchPlaylistSongs,
  getPlaylistIndex
} from "../actions/playlistsActions";
import {
  playSong,
  getSongFeatures,
  getSongIndex
} from "../actions/songActions";

import { withStyles } from "@material-ui/core";
import Drawer from "@material-ui/core/Drawer";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Typography from "@material-ui/core/Typography";

const drawerWidth = 240;

const styles = theme => ({
  root: {
    display: "flex",
    maxHeight: "640px" //height of spotify album cover
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0
  },
  drawerPaper: {
    width: drawerWidth,
    position: "relative",
    background: "transparent",
    overflow: "auto",
    scrollbarWidth: "none",
    "&::-webkit-scrollbar": {
      width: "0",
      height: "0"
    }
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing.unit * 0,
    overflow: "auto",
    scrollbarWidth: "none",
    "&::-webkit-scrollbar": {
      width: "0",
      height: "0"
    }
  },
  table: {
    maxWidth: 500
  },
  tableC: {
    borderBottomColor: theme.palette.divider
  },
  playTitle: {
    position: "absolute",
    marginLeft: "15px",
    marginTop: "-25px"
  },
  tracklistTotal: {
    position: "absolute",
    marginLeft: "265px",
    marginTop: "640px"
  },
  tableRow: {
    "&:hover": {
      cursor: "pointer"
    }
  },
  selectedSongText: {
    color: "#1DB954"
  }
});

class Playlists extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedIndexPL: this.props.playlists.playlistIndex,
      selectedIndexSong: this.props.songIndex
    };
  }

  componentDidUpdate(prevProps) {
    if (this.props !== prevProps) {
      if (this.props.userId !== prevProps.userId) {
        if (this.props.userId !== "" && this.props.token !== "") {
          this.props.fetchPlaylists(this.props.userId, this.props.token);
        }
      }
    }
    if (this.props.songIndex !== prevProps.songIndex) {
      this.setState({ selectedIndexSong: this.props.songIndex });
    }
  }

  renderList() {
    if (this.props.playlists.playlists) {
      return this.props.playlists.playlists.map((playlist, index) => {
        return (
          <ListItem
            variant="body2"
            button
            selected={this.state.selectedIndexPL === index}
            key={playlist.id}
            onClick={() =>
              this.FetchSongs(
                this.props.userId,
                playlist.id,
                this.props.token,
                index
              )
            }
          >
            <ListItemText primary={playlist.name} />
          </ListItem>
        );
      });
    } else {
      return (
        <React.Fragment>
          <ListItem variant="body2" key="generic">
            <ListItemText primary="Loading Playlists..." />
          </ListItem>
        </React.Fragment>
      );
    }
  }

  msToMinutesAndSeconds(ms) {
    const minutes = Math.floor(ms / 60000);
    const seconds = ((ms % 60000) / 1000).toFixed(0);
    return minutes + ":" + (seconds < 10 ? "0" : "") + seconds;
  }

  FetchSongs(userId, playlistId, token, index) {
    //We avoid fetching the same playlist
    if (this.props.songs.playlistId !== playlistId) {
      this.props.fetchPlaylistSongs(userId, playlistId, token);
      this.props.getPlaylistIndex(index);
      this.props.getSongIndex(null);
      //
      this.setState({ selectedIndexPL: index });
      this.setState({ selectedIndexSong: null });
    }
  }

  SelectSongToPlay(song, index) {
    this.props.playAudio(song);
    //this.props.playSong(song.track);
    this.props.getSongFeatures(song.track.id, this.props.token);
    this.props.getSongIndex(index);
    this.setState({ selectedIndexSong: index });
  }

  renderTrackList() {
    const { classes } = this.props;
    if (this.props.songs.songs) {
      return (
        <React.Fragment>
          <div className={classes.content}>
            <Table className={classes.table}>
              <TableHead>
                <TableRow key="headertable">
                  <TableCell className={classes.tableC}>TITLE</TableCell>
                  <TableCell className={classes.tableC} align="left">
                    ARTIST
                  </TableCell>
                  <TableCell className={classes.tableC} align="left">
                    DURATION
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {this.props.songs.songs.map((row, index) => (
                  <TableRow
                    selected={
                      this.state.selectedIndexSong === index ? true : false
                    }
                    className={classes.tableRow}
                    hover={true}
                    key={index + "" + row.track.id}
                    onClick={() => this.SelectSongToPlay(row, index)}
                  >
                    <TableCell
                      className={classes.tableC}
                      component="th"
                      scope="row"
                    >
                      {row.track.name}
                    </TableCell>
                    <TableCell className={classes.tableC} align="left">
                      {row.track.artists[0].name}
                    </TableCell>
                    <TableCell className={classes.tableC} align="left">
                      {this.msToMinutesAndSeconds(row.track.duration_ms)}
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>
        </React.Fragment>
      );
    } else {
      return null;
    }
  }

  render() {
    const { classes } = this.props;
    return (
      <React.Fragment>
        <Typography className={classes.playTitle} variant="overline" noWrap>
          PLAYLISTS
        </Typography>
        <Typography
          className={classes.tracklistTotal}
          variant="overline"
          noWrap
        >
          {this.props.songs.songs
            ? this.props.songs.songs.length + " SONGS"
            : null}
        </Typography>
        <div className={classes.root}>
          <Drawer
            className={classes.drawer}
            variant="permanent"
            classes={{
              paper: classes.drawerPaper
            }}
            anchor="left"
          >
            <List component="nav">{this.renderList()}</List>
          </Drawer>
          {this.renderTrackList()}
        </div>
      </React.Fragment>
    );
  }
}

const mapStateToProps = state => {
  return {
    userId: state.userReducer.user ? state.userReducer.user.id : "",
    token: state.tokenReducer.token ? state.tokenReducer.token : "",
    playlists: state.playlistsReducer,
    songs: state.songsReducer.songs ? state.songsReducer : "",
    selectedSongId: state.songsReducer.songId,
    songIndex: state.songsReducer.songIndex
  };
};

const mapDispatchToProps = {
  fetchPlaylists,
  fetchPlaylistSongs,
  playSong,
  getSongFeatures,
  getSongIndex,
  getPlaylistIndex
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(Playlists));
